.leftFactChartEditor {
  height: calc(100% - 55px) !important;
  overflow-y: auto;
  padding-right: 3px;
}
.ant-col-16 > .ant-col > .ChartConfiguration {
  border: 1px solid #dfdfdf;
  border-radius: 2px;
  text-align: center;
}
.ant-collapse-content > .ant-collapse-content-box {
  padding: 5px 5px 5px 5px;
}
.TopicLabel {
  overflow-y: auto;
  overflow-x: hidden;
}
.TopicLabel .TemporalFilterContent {
  width: 290px;
  background: #c4c4c4;
  margin-bottom: 5px !important;
}
.TopicLabel .NumericalFilter1 {
  margin-top: 5px;
}
.TopicLabel .topicContent1 {
  display: inline-block;
  width: 25%;
  vertical-align: top;
  margin-top: 10px;
  font-size: 10px;
  font-weight: 600;
  margin-left: 2px;
}
.TopicLabel .topicDetailsSales {
  padding-left: 45px !important;
}
.TopicLabel .topicDetails1 {
  display: inline-block;
  width: 65%;
  padding: 5px;
  position: relative;
  background: #FFFFFF;
  border-radius: 2px;
  margin-top: 5px;
  margin-left: 2px;
}
.TopicLabel .topicDetails1 .Salestop {
  width: 40px;
  position: absolute;
  left: -10px;
}
.TopicLabel .topicDetails1 .Salesbot {
  width: 40px;
  position: absolute;
  left: -10px;
  top: 33px;
}
.TopicLabel .topicDetails1 .ant-tag {
  border: 1px solid #c4c4c4;
  border-radius: 2px;
  padding: 2px 5px;
  margin-right: 3px;
  margin-bottom: 3px;
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 14px;
  color: #000000;
}
.TopicLabel .topicDetails1 .ant-tag-checkable-checked {
  background-color: #c4c4c4;
}
.TopicLabel .timeMarkMin {
  border: solid 1px #000;
  border-radius: 2px;
  font-size: 10px;
  padding-left: 2px;
  padding-right: 2px;
  color: #000;
  margin-left: 23px;
}
.TopicLabel .timeMarkMax {
  border: solid 1px #000;
  border-radius: 2px;
  font-size: 10px;
  padding-left: 2px;
  padding-right: 2px;
  margin-right: 17px;
  color: #000;
  margin-left: calc(-20%);
}
.ant-row {
  box-sizing: border-box;
  border-radius: 2px;
  margin-bottom: 3px;
  margin-top: 3px;
}
.ant-row .ant-col-8 {
  background: #5F5F5F;
  border-radius: 2px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 32px;
  /* identical to box height */
  text-align: center;
  color: #FFFFFF;
}
.ant-row .ant-col-16 .ant-row {
  border: none;
  box-sizing: border-box;
  border-radius: 2px;
  margin-bottom: 0px;
}
.ant-row .ant-select {
  width: 100%;
}
.ant-row .ant-btn {
  border: none;
}
.ant-col-18 {
  top: 4px;
}
.ant-row .ant-col-16 {
  border: 1px solid #c0c0c0;
  border-radius: 2px;
}
